<script>
import request from "@/utils/request";

export default {
  name: 'resume',
  data() {
    return {
      resume: {
        id:"",
        userId:"",
        text:""
      }
    }
  },
  mounted() {
    request.get('/user/resume').then(res => {
      console.log(res.data.data);
      this.resume = res.data.data
    })
  },
  methods: {
    save() {
          request.post('/user/saveResume', this.resume).then(res => {
            if (res.data.code === 1) {
            /*  console.log(res.data.data)*/

              this.$message.success("保存成功")
              location.reload()
            } else {
              this.$message.error(res.data.message)
            }
          }
      )

    }
  },
}
</script>
